import { observable, decorate } from 'mobx';

let id = 0;
function createData(name, price, dynamic) {
  id += 1;
  return { id, name, price, dynamic};
}

class CreditPageStore {

  constructor() {
    this.givedCredits = [
        createData('Frozen yoghurt', 159, 6.0),
        createData('Frozen ETH', 159, 6.0),
        createData('Frozen ETH', 159, 6.0),
        createData('Frozen ETH', 159, 6.0),
        createData('Frozen ETH', 159, 6.0),
        createData('Frozen ETH', 159, 6.0),
      ];
    this.creditExamples = '';
  }

  makeCredit(name,type,amount) {
    this.givedCredits.push(createData(name, type,amount))
    console.info(this.givedCredits);
  }

}
decorate(CreditPageStore, { givedCredits: observable })
const creditPageStore = new CreditPageStore();
export default creditPageStore;
export { CreditPageStore };