import React, { Component } from 'react'

import SwipeableViews from 'react-swipeable-views';

import {Paper, Button, TextField, Tabs, Tab, Typography} from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import cryptocurStore from "../../tools/stores/cryptocurStore";

import './styles.css';

const TabContainer = (props) => {
    const { children, dir } = props;
  
    return (
      <Typography component="div" dir={dir}>
        {children}
      </Typography>
    );
  }

  const styles = theme => ({
    container: {
      display: 'flex',
      flexWrap: 'wrap',
      marginTop: '3em',
    },
    textField: {
      marginLeft: theme.spacing.unit,
      marginRight: theme.spacing.unit,
    },
    dense: {
      marginTop: 16,
    },
    menu: {
      width: 200,
    },
  });

class ShopFrame extends Component {
    state = {
        value: 0,
        name: ''
      };
    
      handleChangeVal = (event, value) => {
        this.setState({ value });
      };

      handleChange = name => event => {
        this.setState({
          [name]: event.target.value,
        });
      };
    
      handleChangeIndex = index => {
        this.setState({ value: index });
      };

  render() {
    const { classes } = this.props;

    return (
      <div>
         <Paper className='shop'>

         <Tabs
            value={this.state.value}
            onChange={this.handleChangeVal}
            indicatorColor="primary"
            textColor="secondary"
            fullWidth
          >
            <Tab label={`Купить ${cryptocurStore.coin}`} />
            <Tab label={`Продать ${cryptocurStore.coin}`} />
          </Tabs>

        <SwipeableViews
          index={this.state.value}
          onChangeIndex={this.handleChangeIndex}
        //   className={classes.container}
        >
          <TabContainer >
          <TextField
            id="outlined-name"
            label="Цена"
            type="number"
            margin="normal"
            type="number"
            className={classes.textField}
            value={this.state.price}
            onChange={this.handleChange('price')}
            variant="outlined"
        />
          <TextField
            id="outlined-name"
            label="Кол-во"
            type="number"
            margin="normal"
            type="number"
            className={classes.textField}
            value={this.state.quontity}
            onChange={this.handleChange('quontity')}
            variant="outlined"
        />
          <TextField
            id="outlined-name"
            // className={classes.textField}
            label="Сумма"
            type="number"
            margin="normal"
            value={this.state.quontity*this.state.price || 0}
            disabled
            variant="outlined"
        />
              
                <Button
                    className="shopButton"
                    size="large"
                    onClick={()=>{
                      cryptocurStore.buyCoin(cryptocurStore.coin,this.state.price,this.state.quontity,'buy')
                    }}
                >
                    {`Купить ${cryptocurStore.coin}`}
                </Button>

            </TabContainer>

          <TabContainer
            className={classes.container}
          >
          <TextField
            id="outlined-name"
            label="Цена"
            type="number"
            margin="normal"
            className={classes.textField}
            value={this.state.price}
            onChange={this.handleChange('price')}
            variant="outlined"
        />
          <TextField
            id="outlined-name"
            label="Кол-во"
            margin="normal"
            type="number"
            className={classes.textField}
            value={this.state.quontity}
            onChange={this.handleChange('quontity')}
            variant="outlined"
        />
          <TextField
            type="number"
            id="outlined-name"
            // className={classes.textField}
            label="Сумма"
            margin="normal"
            value={this.state.quontity*this.state.price || 0}
            disabled
            variant="outlined"
        />
              
                <Button
                                   className="shopButton"
                                   size="large"
                                   onClick={()=>{
                                     cryptocurStore.buyCoin(cryptocurStore.coin,this.state.price,this.state.quontity,'sell')
                                   }}
                >
                {`Продать ${cryptocurStore.coin}`}
                </Button>
              </TabContainer>

        </SwipeableViews>
        </Paper>
      </div>
    )
  }
}

export default withStyles(styles)(ShopFrame);
